.App{
  padding: 0;
  margin:0;
  overflow: hidden;
}
article, aside, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}

.spyre-navbar {
  height: 60px;
  flex-wrap: nowrap;
  background-color: rgba(31, 39, 73, 0)!important;
  transition: all .3s;
}
.spyre-navbar.sticky{
  background-color: rgb(31, 39, 73)!important;
}
.mt-n3{
  margin-top: -35px!important;
}
.spyre-navbar .navbar-brand {
  margin-top: 20px;
}

.spyre-navbar .navbar-text {
  font-size: 1rem;
  font-weight: 500;
  position: relative;
  transition: color .5s ease;
}

.spyre-navbar .navbar-text a {
  transition: color .5s ease;
  text-decoration: none;
}
.pl-2, .px-2 {
  padding-left: .5rem!important;
}
.pl-5, .px-5 {
  padding-left: 3rem!important;
}

.search {
  position: relative;
}
.search .form-control {
  font-size: 30px;
  font-weight: 700;
  position: absolute;
  right: 50px;
  width: 0;
  padding: 0;
  transition: width .5s ease-in-out;
  color: #fff;
  border: none;
  border-bottom: 1px solid rgba(255,255,255,.3);
  background: 0 0;
  border-radius: 0px;
}
.search .form-control:focus {
  color: #fff;
  border: 0px solid black;
  border-bottom: 1px solid rgb(255, 250, 250);
  background: 0 0;
  box-shadow: none;
}


.search i {
  font-size: 2.3rem;
  cursor: pointer;
  color: #fff;
}
.menu-toggle {
  position: relative;
  top: -1px;
  left: 3px;
  width: 37px;
  min-width: 37px;
  height: 37px;
  cursor: pointer;
}
.menu-toggle .hamburger {
  position: absolute;
  width: 37px;
  height: 37px;
}

.menu-toggle .hamburger span:nth-child(1) {
  top: 8px;
  transition-delay: .5s;
}
.menu-toggle .hamburger span:nth-child(2) {
  top: 17px;
  width: 20px;
  transition-delay: .625s;
}
.menu-toggle .hamburger span:nth-child(3) {
  bottom: 8px;
  width: 24px;
  transition-delay: .75s;
}
.menu-toggle .hamburger span {
  position: absolute;
  right: 4px;
  width: 30px;
  height: 3px;
}

.menu-toggle .cross {
  position: absolute;
  width: 100%;
  height: 100%;
  transform: rotate(
45deg);
}
.menu-toggle .cross span:nth-child(1) {
  top: 0;
  left: 17px;
  width: 3px;
  height: 0;
  transition-delay: 0s;
}
.menu-toggle .cross span:nth-child(2) {
  top: 17px;
  left: 0;
  width: 0;
  height: 3px;
  transition-delay: .25s;
}
.menu-toggle .cross span {
  position: absolute;
}
.menu-toggle span {
  transition: .25s ease-in-out;
  transition-property: top,left,width,height;
  background: #fff;
}

.overlay-slide {
  top: 0;
  right: 0;
  left: auto;
  width: 0;
  height: auto;
  min-height: 100%;
  transition: all .3s;
  transition-timing-function: cubic-bezier(.7,1,.3,1);
}

.spyre-navbar-overlay {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  visibility: hidden;
  overflow: hidden;
  width: 100%;
  height: 100%;
  background: rgba(31,39,73,.995);
}
.spyre-navbar-nav-container {
  height: 100vh;
}
.ext-l::before {
  right: 100%;
}

.spyre-navbar-nav {
  position: relative;
  top: 82px;
  display: flex;
  overflow-x: hidden;
  overflow-y: auto;
  width: 100%;
  height: calc(100vh - 82px);
  padding: 0 15px;
  animation: scale-down-ver-center .5s cubic-bezier(.39,.575,.565,1) both;
  align-items: center;
}
.ext-l::after, .ext-l::before, .ext-r::after, .ext-r::before {
  position: absolute;
  top: 0;
  bottom: 0;
  content: '';
  background-color: inherit;
}
.spyre-nav {
  width: 100%;
  height: auto;
  margin: auto 0;
  padding: 0;
  list-style: none;
  list-style-type: none;
  text-align: center;
}
.spyre-nav-item {
  margin-bottom: .75rem;
}
.spyre-nav-link {
  font-size: 1.875rem;
  font-weight: 300;
  line-height: 2.25rem;
  position: relative;
  display: block;
  transition: color .3s ease;
  text-transform: uppercase;
  text-decoration: none;
  color: #1f2749;
}
.spyre-nav-link:hover {
  text-decoration: none;
  color: #f12639;
}
/* navsise open*/
.menu-toggle.open .hamburger span:nth-child(1) {
  transition-delay: 0s;
}
.menu-toggle.open .hamburger span:nth-child(2) {
  transition-delay: .1s;
}
.menu-toggle.open .hamburger span:nth-child(3) {
  transition-delay: .2s;
}
.menu-toggle.open .hamburger span {
  width: 0;
}
.menu-toggle.open .cross span:nth-child(1) {
  height: 37px;
  transition-delay: .5s;
}
.menu-toggle.open .cross span:nth-child(2) {
  width: 37px;
  transition-delay: .3s;
}
.menu-toggle.open .cross span {
  background: #f12639;
}

.overlay-slide.open {
  top: 0;
  right: 0;
  width: 100%;
  min-height: 100%;
}
.spyre-navbar-overlay.open {
  visibility: visible;
}
.spyre-navbar-nav.open {
  animation: scale-up-ver-center .5s cubic-bezier(.39,.575,.565,1) .8s both;
}
.search.open {
  width: 100%;
}
.search.open .form-control {
  width: calc(100% - 100px);
}
.search.open i {
  color: #f12639;
}

